<template>
  <div>
    <div class="row">
      <div class="col-md-12">
          <div class="card">
            <div v-if="isRunning == false" class="card-body">
              <div class="row">
                <div class="col-md-3">
                  Seconds per exercice<b-input type="number" v-model.number="exercice_time" placeholder="Nbr"></b-input>
                </div>
                <div class="col-md-3">
                  Seconds between exercices<b-input type="number" v-model.number="time_between_exercices" placeholder="Nbr"></b-input>
                </div>
                <div class="col-md-3">
                  Series of<b-input type="number" v-model.number="series_of" placeholder="Nbr"></b-input>
                </div>
                <div class="col-md-3">
                  Seconds between series<b-input type="number" v-model.number="time_between_series" placeholder="Time"></b-input>
                </div>
              </div>
              <p/>
              <div class="row">
                <div class="col-md-3">
                  <b-button variant="primary"  @click="startExercice()">Start Exercice</b-button>
                </div>
              </div>
            </div>
            <div v-else>
              <div class="card-body" style="height: 70vh;">
                <div class="display-4 text-center font-weight-bold">
                  <font size="12">{{ current_workout }}</font>
                </div>
                <div class="text-center font-weight-bolder super-huge">
                  {{ time - this.time_between_exercices }}
                </div>
                <div v-if="this.current_list.length > 0" class="display-4 text-center">
                  <font size="5">Next</font><br/>
                  <font size="7">{{ this.generated_workout[0]["name"] }}</font>
                </div>
              </div>
              <div style="height: 25vh;">
                <div v-if="paused == false" class="col-md-12 paddit">
                  <b-button block variant="primary" @click="pauseExercice()">Pause Exercice</b-button>
                </div>
                <div v-else class="col-md-12 paddit">
                  <b-button block  variant="primary" @click="resumeExercice()">Resume Exercice</b-button>
                </div>
                <div class="col-md-12 paddit">
                  <b-button block  variant="warning" @click="skipExercice()">Skip Exercice</b-button>
                </div>
                <div class="col-md-12 paddit">
                  <b-button block  variant="danger" @click="stopExercice()">Stop Exercice</b-button>
                </div>
              </div>
            </div>
        </div>
      </div>
    </div>
    <p/>
    <div class="row" v-if="isRunning == false">
      <div class="col-md-4" style="margin-top:20px;">
        <div class="card">
          <div class="card-header">
            Add to current workout
          </div>
          <div class="card-body">
            <div class="paddit" v-for="element in available_workouts" :key="element.id">
               <b-button block class="btn btn-primary btn-sm" varient="small" @click="addToWorkout(element)">{{element.name}}</b-button>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-8" style="margin-top:20px;">
        <div class="card">
          <div class="card-header">
            Current Workout
            <div class="float-right" >
              <b-button class="btn-sm" variant="primary" @click="shuffle()"><b-icon icon="shuffle"></b-icon></b-button>&nbsp;
              <b-button class="btn-sm" variant="danger" @click="removeAll()"><b-icon icon="trash"></b-icon></b-button>
            </div>
          </div>
          <div class="card-body" style="width: 95%;">
            <draggable v-model='current_list'>
              <div v-for="(element, index) in current_list" :key="index">
                <div class="small-alert">
                  {{element.name}} <b-button class="float-right btn-danger btn-sm" @click="remove(index)"><b-icon icon="trash"></b-icon></b-button>
                  <hr/>
                </div>
              </div>
            </draggable>
          </div>
        </div>
        <p class="bottom-buffer"/>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import NoSleep from 'nosleep.js/dist/NoSleep'

export default {
  components: {
    draggable,
  },
  name: 'Home',
  data: function() {
    return {
      add_number:null,
      time:0,
      time_between_exercices: 3,
      exercice_time: 45,
      series_of: 2,
      time_between_series: 15,
      isRunning: false,
      generated_workout: [],
      current_workout: "",
      timer:null,
      current_serie:0,
      paused: false,
      audio: new Audio("http://soundbible.com/mp3/Bleep-SoundBible.com-1927126940.mp3"),
      noSleep: new NoSleep()
    }
  },
  props: {
  },
  computed: {
    available_workouts: {
      get() {
        return this.$store.getters["workout/getConfig"]["workouts"];
      }
    },
    current_list: {
        get() {
            return this.$store.getters["workout/getCurrentWorkout"]
        },
        set(value) {
          this.$store.commit('workout/updateCurrentWorkout', value)
        }
    }
  },
  methods: {
    shuffle(){
      this.current_list.sort(() => Math.random() > .5 ? -1 : 1);
    },
    addToWorkout(element) {
      for (var activity in element.activities) {
        this.current_list.push({"name": element.activities[activity]});
      }
    },
    addAll(){
      for (var i=0; i<this.available_workouts.length;i++) {
        this.current_list.push(this.available_workouts[i]);
      }
    },
    removeAll(){
        this.current_list.splice(0, this.current_list.length)
    },
    remove(index) {
      this.current_list.splice(index, 1);
    },
    addQty(number) {
      for (var i=0; i<number; i++) {
        var item_index = Math.floor(Math.random() * Math.floor(this.available_workouts.length));
        this.current_list.push(this.available_workouts[item_index]);
      }
    },
    startExercice() {
      this.noSleep.enable();
      this.isRunning = true
      this.current_workout = "Get Ready!"
      this.time = 3 + this.time_between_exercices
      this.audio.volume = 0.2;
      this.audio.play();
      this.generated_workout = []
      var pause_counter = 0;
      for (var workout in this.current_list) {
        this.generated_workout.push({"name": this.current_list[workout]["name"], "time": this.exercice_time + this.time_between_exercices})
        pause_counter += 1;
        if (pause_counter != 0 && pause_counter % this.series_of == 0) {
          this.generated_workout.push({"name": "Pause", "time": this.time_between_series + this.time_between_exercices })
        }
      }

      if (!this.timer) {
          this.timer = setInterval( () => {
            if (this.paused || this.isRunning == false) {
              return
            }else if (this.time > 0) {
               this.time--
            } else if (this.generated_workout.length > 0) {
              this.current_workout = this.generated_workout[0]["name"]
              this.time = this.generated_workout[0]["time"]
              this.generated_workout.shift()
            } else {
               this.current_workout = "Workout Over!"
               clearInterval(this.timer)
               this.reset()
            }
            var ping_time = this.time - this.time_between_exercices
            if (ping_time == 3  || ping_time == 2 || ping_time == 1 || ping_time == 0 || this.time == 0) {
              this.audio.play();
              this.audio.currentTime = 0
            }
          }, 1000 )
       }
    },
    stopExercice () {
      this.noSleep.disable();
      this.isRunning = false
      clearInterval(this.timer)
      this.timer = null
      this.paused = false
      this.time = 0
    },
    pauseExercice () {
      this.paused = true
    },
    skipExercice () {
      this.time = 0
    },
    resumeExercice () {
      this.paused = false
    },
  }
};
</script>

