import axios from 'axios';



export default {
  namespaced: true,
  state: {
    current_workout: [],
    config: {}
  },
  mutations: {
    getConfig(state, config) {
      state.config = config;
    },
    updateCurrentWorkout(state, value){
      state.current_workout = value;
    }
  },
  actions: {
    getConfig({ commit }) {
      axios.get('/api/get_config')
        .then(function(result) {
          return commit('getConfig', result.data)
        })
        .catch(console.error);
    },
  },
  getters: {
    getConfig(state) {
      return state.config;
    },
    getCurrentWorkout(state) {
      return state.current_workout;
    }
  }
};

